$site_max_width: 960px;
$font_color: #333;
$link_color: #00c;
$font_family: Arial, sans-serif;
$font_size: 16px;
$line_height: percentage(20px / $font_size);
body {
  color: $font_color;
  font: {
    size: $font_size;
    family: $font_family; }
  line-height: $line_height; }

#main {
  width: 100%;
  max-width: $site_max_width; }
